@font-face {
	font-family: "Calibri";
	font-display: swap;
	src: url("./fonts/calibri.woff") format("woff"), url("./fonts/calibri.woff2") format("woff2");
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: "Calibri";
	font-display: swap;
	src: url("./fonts/calibrib.woff") format("woff"), url("./fonts/calibrib.woff2") format("woff2");
	font-weight: 900;
	font-style: normal;
}

body {
	margin: 0;
	padding: 0;
	font-family: "Calibri", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
		"Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif !important;
}
