.news{background:#fff}.news::before{content:"";width:100%;height:60px;display:block;background-color:#1a1a1a}.news__inner{background-color:#fff;position:relative;top:-60px;padding:0px 35px}.news__inner h3{text-align:center;padding:10px 0}.news__inner .swiper-container{height:200px}.news__card_big{margin-left:15px;margin-bottom:20px}.news__card_big .row{padding:5px 0;background:#bbe9fe;background:-webkit-gradient(linear, left top, right top, from(#bbe9fe), color-stop(80%, #fefefe));background:linear-gradient(90deg, #bbe9fe 0%, #fefefe 80%)}.news__card_big span{font-size:28px;font-weight:400}.news__card_big h5{font-size:17px;font-weight:400;margin:0}.news__card_big p{font-size:14px;font-weight:400;margin:0}
