.partnersSlider{background:url("../img/partnersPageBg.png") 0 0 no-repeat;background-size:cover;padding-bottom:50px;border-bottom:5px solid #fff}.partnersSlider .slick-list{padding-bottom:15px}.partnersSlider h3{color:#ffffff;text-align:center;font-size:23px;padding:15px 0;font-weight:400}.partnersSlider__slide{text-align:center}.partnersSlider__slide img{display:inline-block}.requestForm input{border-radius:0;border:none;background:#bcebff;background:-webkit-gradient(linear, left top, right top, from(#bcebff), to(rgba(255,255,255,0)));background:linear-gradient(90deg, #bcebff 0%, rgba(255,255,255,0) 100%)}.requestForm button{border-radius:0;background:#1272b5;background:-webkit-gradient(linear, left bottom, left top, from(#1272b5), to(#33a8e5));background:linear-gradient(0deg, #1272b5 0%, #33a8e5 100%)}.requestForm label{color:#706f6f;margin-bottom:0}.requestForm__wrapper{background:#e0f5fe;background:-webkit-gradient(linear, left top, right top, from(#e0f5fe), color-stop(20%, #fff), to(#fff));background:linear-gradient(90deg, #e0f5fe 0%, #fff 20%, #fff 100%)}.requestForm__wrapper::before{content:"";width:100%;height:60px;position:relative;top:-1px;display:block;background-color:#1a1a1a}.requestForm__inner{background-color:#fff;position:relative;top:-60px;padding:0px 35px}.requestForm__inner h3{text-align:center;padding-top:20px;padding-bottom:10px;font-size:23px;font-weight:400;color:#000}
